'use client';

import React from 'react';
import { motion } from 'framer-motion';
import { ChevronLeft, ChevronRight, Play, Pause, RotateCcw, BookOpen, Book, Sun, Moon, Hash } from 'lucide-react';
import { useState, useEffect, useRef } from 'react';

interface ControlPanelProps {
  currentPage: number;
  numPages: number;
  onPrev: () => void;
  onNext: () => void;
  onAutoRoll: () => void;
  isAutoRolling: boolean;
  onReset: () => void;
  onToggleView: () => void;
  isSinglePage: boolean;
  onToggleTheme: () => void;
  isDarkMode: boolean;
  onPageInput: (page: number) => void;
}

const ControlPanel: React.FC<ControlPanelProps> = ({
  currentPage,
  numPages,
  onPrev,
  onNext,
  onAutoRoll,
  isAutoRolling,
  onReset,
  onToggleView,
  isSinglePage,
  onToggleTheme,
  isDarkMode,
  onPageInput,
}) => {
  const [showInput, setShowInput] = useState(false);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if (showInput && inputRef.current) {
      inputRef.current.focus();
    }
  }, [showInput]);

  const handlePageSubmit = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      const val = parseInt(e.currentTarget.value);
      if (!isNaN(val) && val >= 1 && val <= numPages) {
        onPageInput(val);
        setShowInput(false);
      }
    } else if (e.key === 'Escape') {
      setShowInput(false);
    }
  };

  return (
    <motion.div
      initial={{ y: 100 }}
      animate={{ y: 0 }}
      className="fixed bottom-8 left-1/2 transform -translate-x-1/2 bg-gray-800/90 backdrop-blur-md text-white px-6 py-3 rounded-full shadow-2xl flex items-center gap-6 z-50 border border-gray-700"
    >
      <div className="flex items-center gap-2">
        <button
          onClick={onPrev}
          disabled={currentPage <= 1}
          className="p-2 hover:bg-white/10 rounded-full disabled:opacity-50 transition-colors"
          title="Previous Page"
        >
          <ChevronLeft className="w-5 h-5" />
        </button>
        
        <div className="flex items-center justify-center min-w-[100px]">
          {showInput ? (
            <div className="flex items-center gap-1">
              <input 
                ref={inputRef}
                type="number" 
                min={1} 
                max={numPages}
                defaultValue={currentPage}
                onBlur={() => setShowInput(false)}
                onKeyDown={handlePageSubmit}
                className="w-12 bg-gray-700 border border-gray-600 rounded px-1 text-center text-sm focus:outline-none focus:border-blue-500"
              />
              <span className="text-sm font-medium">/ {numPages}</span>
            </div>
          ) : (
            <span className="text-sm font-medium">
              Page {currentPage} / {numPages || '--'}
            </span>
          )}
        </div>

        <button
          onClick={onNext}
          disabled={currentPage >= numPages}
          className="p-2 hover:bg-white/10 rounded-full disabled:opacity-50 transition-colors"
          title="Next Page"
        >
          <ChevronRight className="w-5 h-5" />
        </button>
      </div>

      <div className="w-px h-6 bg-gray-600" />

      <div className="flex items-center gap-2">
        <button
          onClick={() => setShowInput(true)}
          className="p-2 hover:bg-white/10 rounded-full transition-colors"
          title="Go to Page"
        >
          <Hash className="w-5 h-5" />
        </button>

        <button
          onClick={onToggleView}
          className="p-2 hover:bg-white/10 rounded-full transition-colors"
          title={isSinglePage ? "Switch to Double Page" : "Switch to Single Page"}
        >
          {isSinglePage ? <BookOpen className="w-5 h-5" /> : <Book className="w-5 h-5" />}
        </button>

        <button
          onClick={onToggleTheme}
          className="p-2 hover:bg-white/10 rounded-full transition-colors"
          title={isDarkMode ? "Switch to Light Mode" : "Switch to Dark Mode"}
        >
          {isDarkMode ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
        </button>

        <button
          onClick={onAutoRoll}
          className={`p-2 rounded-full transition-colors ${
            isAutoRolling ? 'bg-blue-500 text-white' : 'hover:bg-white/10'
          }`}
          title={isAutoRolling ? 'Stop Auto-roll' : 'Start Auto-roll'}
        >
          {isAutoRolling ? <Pause className="w-5 h-5" /> : <Play className="w-5 h-5" />}
        </button>

        <button
          onClick={onReset}
          className="p-2 hover:bg-white/10 rounded-full transition-colors text-red-400 hover:text-red-300"
          title="Reset Viewer"
        >
          <RotateCcw className="w-5 h-5" />
        </button>
      </div>
    </motion.div>
  );
};

export default ControlPanel;
