'use client';

import React, { useState, useRef, useCallback, forwardRef, useEffect } from 'react';
import HTMLFlipBook from 'react-pageflip';
import { Document, Page, pdfjs } from 'react-pdf';
import { motion } from 'framer-motion';
import { Loader2 } from 'lucide-react';

// Configure PDF worker
pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.mjs`;

interface FlipBookProps {
  file: File | string;
  onPageChange?: (page: number) => void;
}

interface PageProps {
  pageNumber: number;
  width: number;
  height: number;
  density?: 'soft' | 'hard';
}

// ForwardRef is needed for HTMLFlipBook children
const PDFPage = forwardRef<HTMLDivElement, PageProps>(({ pageNumber, width, height, density = 'soft' }, ref) => {
  return (
    <div ref={ref} className="bg-white shadow-md overflow-hidden" data-density={density}>
      <div className="relative w-full h-full flex items-center justify-center">
        <Page
          pageNumber={pageNumber}
          width={width}
          height={height}
          className="w-full h-full"
          renderTextLayer={false}
          renderAnnotationLayer={false}
        />
        {/* Page shadow/gradient overlay for realism */}
        <div className="absolute inset-0 pointer-events-none bg-gradient-to-r from-black/5 via-transparent to-transparent" />
      </div>
    </div>
  );
});

PDFPage.displayName = 'PDFPage';

interface FlipBookProps {
  file: File | string;
  onPageChange?: (page: number) => void;
  onLoadSuccess?: (data: { numPages: number }) => void;
  usePortrait?: boolean;
}

const FlipBook = forwardRef<any, FlipBookProps>(({ file, onPageChange, onLoadSuccess, usePortrait = false }, ref) => {
  const [numPages, setNumPages] = useState<number>(0);
  const containerRef = useRef<HTMLDivElement>(null);
  const [containerSize, setContainerSize] = useState({ width: 0, height: 0 });

  useEffect(() => {
    const element = containerRef.current;
    if (!element) return;

    const resizeObserver = new ResizeObserver((entries) => {
      for (const entry of entries) {
        const { width, height } = entry.contentRect;
        setContainerSize({ width, height });
      }
    });

    resizeObserver.observe(element);
    return () => resizeObserver.disconnect();
  }, []);

  const { width: containerWidth, height: containerHeight } = containerSize;

  const onDocumentLoadSuccess = (data: { numPages: number }) => {
    setNumPages(data.numPages);
    onLoadSuccess?.(data);
  };

  // Calculate optimal book dimensions based on container
  // Enforce A4 aspect ratio (1 / sqrt(2) â‰ˆ 0.707) for realistic PDF look
  const ASPECT_RATIO = 0.707; // Width / Height
  
  let bookHeight = containerHeight ? containerHeight * 0.9 : 600;
  let bookWidth = bookHeight * ASPECT_RATIO;

  // If calculated width is too wide for container
  // In portrait (single page), we check against full width
  // In landscape (double page), we check against half width (since width is per page)
  const maxTotalWidth = containerWidth * 0.95; // Use 95% of container width
  const currentTotalWidth = usePortrait ? bookWidth : bookWidth * 2;

  if (containerWidth && currentTotalWidth > maxTotalWidth) {
    if (usePortrait) {
      bookWidth = maxTotalWidth;
    } else {
      bookWidth = maxTotalWidth / 2;
    }
    bookHeight = bookWidth / ASPECT_RATIO;
  }

  // Round values to avoid sub-pixel rendering issues
  bookHeight = Math.floor(bookHeight);
  bookWidth = Math.floor(bookWidth);

  // Mouse wheel handler for flipping
  const handleWheel = useCallback((e: React.WheelEvent) => {
    if (e.deltaY > 0) {
      // Scroll down -> Next page
      // Debounce or check if flip is ready? react-pageflip handles some of this, but let's trigger it.
      // We need access to the book instance.
      // Since we are in the render, we can't easily access ref.current.pageFlip() directly here without a wrapper or useEffect.
      // But wait, we can't attach onWheel to the HTMLFlipBook directly if it doesn't support it.
      // We can attach it to the container div.
    }
  }, []);
  
  // We'll expose the flip method via ref to parent, and parent can handle wheel?
  // Or just handle it here if we can get the instance.
  // Actually, let's just fix the mobile layout first.
  
  // Mobile fix: Ensure container has enough height
  // The container is h-full, but parent is absolute inset-0.
  
  return (
    <div 
      ref={containerRef} 
      className="w-full h-full flex items-center justify-center bg-transparent overflow-hidden relative p-4"
    >
      {!containerWidth ? (
        <div className="text-white flex items-center gap-2">
          <Loader2 className="animate-spin" /> Loading Viewer...
        </div>
      ) : (
        <Document
          file={file}
          onLoadSuccess={onDocumentLoadSuccess}
          loading={
            <div className="text-white flex items-center gap-2">
              <Loader2 className="animate-spin" /> Loading PDF...
            </div>
          }
          className="flex justify-center items-center"
        >
          {numPages > 0 && (
            // @ts-ignore - react-pageflip types can be tricky
            <HTMLFlipBook
              key={usePortrait ? 'portrait' : 'landscape'} // Force re-render on view change
              width={bookWidth}
              height={bookHeight}
              size="fixed"
              minWidth={200} // Lower min width for mobile
              maxWidth={1000}
              minHeight={300} // Lower min height for mobile
              maxHeight={1533}
              maxShadowOpacity={0.5}
              showCover={true}
              mobileScrollSupport={true} // This should handle touch scroll
              className="shadow-2xl"
              ref={ref}
              onFlip={(e: any) => onPageChange?.(e.data)}
              flippingTime={1000}
              usePortrait={usePortrait}
              startZIndex={0}
              autoSize={true}
              drawShadow={true}
              showPageCorners={true}
              disableFlipByClick={false}
            >
              {Array.from(new Array(numPages), (el, index) => (
                <PDFPage
                  key={`page_${index + 1}`}
                  pageNumber={index + 1}
                  width={bookWidth}
                  height={bookHeight}
                />
              ))}
            </HTMLFlipBook>
          )}
        </Document>
      )}
    </div>
  );
});

FlipBook.displayName = 'FlipBook';

export default FlipBook;
